.projects-section {
  padding-top: 6rem;
  padding-bottom: 3rem;
}

.project-filters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 3.5rem;
  padding: 0.5rem 1rem;
  border-radius: 12px;
  background: rgba(248, 250, 252, 0.5);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.03);
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  z-index: 1;
  align-items: center;
}

.filter-icon {
  color: var(--primary-color);
  margin-right: 0.5rem;
}

body.dark .project-filters {
  background: rgba(49, 46, 129, 0.2);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
}

.filter-btn {
  background: transparent;
  color: var(--text-color);
  border: none;
  padding: 0.7rem 1.6rem;
  border-radius: 8px;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  font-family: 'Inter', sans-serif;
  letter-spacing: 0.3px;
}

.filter-btn:hover {
  transform: translateY(-2px);
  background: rgba(129, 140, 248, 0.1);
  color: var(--primary-color);
}

.filter-btn.active {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  box-shadow: 0 4px 12px rgba(79, 70, 229, 0.25);
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 2.5rem;
  margin-top: 1rem;
}

.project-card {
  background-color: var(--card-bg);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: var(--shadow);
  transition: all 0.4s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(79, 70, 229, 0.1);
  position: relative;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-card:hover::before {
  opacity: 1;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 30px rgba(79, 70, 229, 0.12);
}

.project-image-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
}

.project-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.6s ease;
}

.project-card:hover .project-image {
  transform: scale(1.05);
}

.project-image-container::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, 
    rgba(79, 70, 229, 0.1), 
    rgba(79, 70, 229, 0)
  );
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-card:hover .project-image-container::after {
  opacity: 1;
}

.project-image-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(145deg, var(--primary-color), var(--secondary-color));
  display: flex;
  justify-content: center;
  align-items: center;
}

.project-image-placeholder span {
  font-size: 3rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.8);
  font-family: 'Manrope', sans-serif;
}

.project-links {
  position: absolute;
  top: 12px;
  right: 12px;
  display: flex;
  gap: 1rem;
  z-index: 2;
}

.project-links a {
  width: 2.5rem;
  height: 2.5rem;
  background-color: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  font-size: 1.1rem;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  opacity: 0.9;
  transform: translateY(5px);
}

.project-card:hover .project-links a {
  transform: translateY(0);
  opacity: 1;
}

.project-links a:hover {
  background-color: var(--primary-color);
  color: white;
  transform: translateY(-3px) scale(1.1);
  box-shadow: 0 5px 15px rgba(79, 70, 229, 0.3);
}

body.dark .project-links a {
  background-color: rgba(49, 46, 129, 0.85);
  color: white;
  border: 1px solid rgba(129, 140, 248, 0.3);
}

body.dark .project-links a:hover {
  background-color: var(--primary-color);
  color: white;
  transform: translateY(-3px);
}

.project-content {
  padding: 1.5rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
}

.project-content h3 {
  font-size: 1.3rem;
  margin-bottom: 1rem;
  color: var(--text-color);
  font-family: 'Manrope', sans-serif;
  font-weight: 700;
  letter-spacing: -0.3px;
  position: relative;
  display: inline-block;
}

.project-content h3::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 40px;
  height: 2px;
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  transition: width 0.3s ease;
}

.project-card:hover .project-content h3::after {
  width: 100%;
}

.project-content p {
  margin-bottom: 1.5rem;
  color: var(--text-color);
  opacity: 0.8;
  line-height: 1.7;
  font-size: 0.95rem;
  flex-grow: 1;
  font-family: 'Inter', sans-serif;
  font-weight: 300;
  letter-spacing: 0.2px;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.9rem;
  margin-bottom: 1.2rem;
}

.tech-icon {
  color: var(--primary-color);
  font-size: 1.3rem;
  transition: all 0.3s ease;
  background-color: rgba(79, 70, 229, 0.08);
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  border: 1px solid rgba(79, 70, 229, 0.1);
}

body.dark .tech-icon {
  background-color: rgba(129, 140, 248, 0.1);
  border: 1px solid rgba(129, 140, 248, 0.2);
}

.tech-icon:hover {
  transform: translateY(-4px) scale(1.1);
  color: white;
  background-color: var(--primary-color);
  box-shadow: 0 4px 12px rgba(79, 70, 229, 0.2);
}

.project-categories {
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
}

.project-category {
  background: linear-gradient(135deg, rgba(79, 70, 229, 0.1), rgba(129, 140, 248, 0.2));
  color: var(--primary-color);
  padding: 0.35rem 0.9rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  letter-spacing: 0.2px;
  font-family: 'Inter', sans-serif;
  border: 1px solid rgba(79, 70, 229, 0.12);
  transition: all 0.3s ease;
  box-shadow: 0 2px 5px rgba(79, 70, 229, 0.05);
}

body.dark .project-category {
  background: linear-gradient(135deg, rgba(79, 70, 229, 0.2), rgba(129, 140, 248, 0.1));
  color: var(--accent-color);
  border: 1px solid rgba(129, 140, 248, 0.2);
}

.project-category:hover {
  transform: translateY(-2px) scale(1.03);
  box-shadow: 0 4px 10px rgba(79, 70, 229, 0.15);
}

.no-projects {
  text-align: center;
  padding: 3rem 0;
}

.no-projects p {
  font-size: 1.1rem;
  color: var(--text-color);
  opacity: 0.7;
}

.project-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.project-card:hover .project-image {
  transform: scale(1.05);
}

/* Tablette */
@media screen and (max-width: 992px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    gap: 1.75rem;
  }
}

/* Mobile Large */
@media screen and (max-width: 768px) {
  .projects-section {
    padding-top: 5rem;
    padding-bottom: 2rem;
  }
  
  .projects-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1.5rem;
  }
  
  .project-filters {
    gap: 0.5rem;
    margin-bottom: 2rem;
    padding: 0.4rem;
    max-width: 95%;
    padding: 0 1rem;
  }
  
  .filter-btn {
    padding: 0.4rem 0.8rem;
    font-size: 0.8rem;
    border-radius: 20px;
  }
  
  .project-image-placeholder span {
    font-size: 2.5rem;
  }
}

/* Mobile Petit */
@media screen and (max-width: 480px) {
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .project-content h3 {
    font-size: 1.1rem;
  }
  
  .project-links a {
    width: 2rem;
    height: 2rem;
    font-size: 1rem;
  }
  
  .project-content {
    padding: 1rem;
  }
}