/* Updated About Styles */

.about-section {
  padding-top: 100px;
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}

.about-bio h2,
.about-skills h2 {
  font-size: 1.8rem;
  margin-bottom: 1.8rem;
  position: relative;
  display: flex;
  align-items: center;
  font-family: 'Manrope', sans-serif;
  letter-spacing: -0.5px;
  font-weight: 700;
}

.about-bio h2::after,
.about-skills h2::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 80px;
  height: 3px;
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  transition: width 0.3s ease;
}

h2.with-icon .section-icon {
  margin-right: 10px;
  color: var(--primary-color);
}

.about-bio p {
  margin-bottom: 1.5rem;
  line-height: 1.8;
  color: var(--text-color);
  font-family: 'Inter', sans-serif;
}

/* Timeline styles */
.timeline {
  margin-top: 3rem;
  position: relative;
}

.timeline::before {
  content: '';
  position: absolute;
  top: 40px;
  left: 8px;
  height: calc(100% - 80px);
  width: 2px;
  background: linear-gradient(to bottom, var(--primary-color), var(--accent-color));
  border-radius: 1px;
  opacity: 0.7;
}

.timeline-item {
  position: relative;
  padding-left: 40px;
  margin-bottom: 2.5rem;
  transition: all 0.3s ease;
}

.timeline-icon {
  position: absolute;
  left: 2px;
  top: 8px;
  width: 20px;
  height: 20px;
  z-index: 2;
  color: var(--primary-color);
}

.timeline-dot {
  position: absolute;
  left: 0;
  top: 7px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--primary-color), var(--accent-color));
  border: 3px solid var(--background-color);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
  z-index: 1;
  transition: all 0.3s ease;
}

.timeline-item:hover .timeline-dot {
  transform: scale(1.2);
  box-shadow: 0 0 0 5px rgba(79, 70, 229, 0.2);
}

.timeline-content h3 {
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  font-family: 'Manrope', sans-serif;
  letter-spacing: -0.3px;
  font-weight: 700;
  color: var(--text-color);
}

.timeline-period {
  font-size: 0.9rem;
  color: var(--primary-color);
  margin-bottom: 0.7rem;
  font-weight: 600;
  opacity: 0.8;
  font-family: 'Inter', sans-serif;
  letter-spacing: 0.2px;
  display: inline-block;
  padding: 0.2rem 0.7rem;
  background: rgba(79, 70, 229, 0.08);
  border-radius: 4px;
  border: 1px solid rgba(79, 70, 229, 0.1);
}

.timeline-content p {
  font-family: 'Inter', sans-serif;
  line-height: 1.7;
  font-weight: 400;
  color: var(--text-color);
  opacity: 0.9;
}

/* Skills styles */
.skills-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.skill-group {
  background-color: var(--card-bg);
  border-radius: 16px;
  padding: 1.75rem;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
  border: 1px solid rgba(79, 70, 229, 0.1);
  position: relative;
  overflow: hidden;
}

.skill-group::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(to bottom, var(--primary-color), var(--secondary-color));
  border-radius: 2px;
  opacity: 0.6;
}

.skill-group:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 20px rgba(79, 70, 229, 0.1);
  border-color: rgba(79, 70, 229, 0.2);
}

.skill-header {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}

.skill-icon {
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, rgba(79, 70, 229, 0.1), rgba(129, 140, 248, 0.2));
  color: var(--primary-color);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  margin-right: 1rem;
  transition: all 0.3s ease;
  border: 1px solid rgba(79, 70, 229, 0.12);
  box-shadow: 0 4px 10px rgba(79, 70, 229, 0.08);
}

.skill-group:hover .skill-icon {
  transform: scale(1.1) rotate(5deg);
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  box-shadow: 0 6px 12px rgba(79, 70, 229, 0.2);
}

.skill-header h3 {
  font-size: 1.3rem;
  font-weight: 700;
  font-family: 'Manrope', sans-serif;
  letter-spacing: -0.3px;
  color: var(--text-color);
}

.skill-list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.9rem;
  margin-top: 1.2rem;
  padding-left: 0.5rem;
}

.skill-list li {
  position: relative;
  padding-left: 20px;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  color: var(--text-color);
  opacity: 0.9;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

.skill-list li::before {
  content: '•';
  position: absolute;
  left: 0;
  color: var(--primary-color);
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 1;
  transition: all 0.3s ease;
}

.skill-list li:hover {
  padding-left: 25px;
  color: var(--primary-color);
  opacity: 1;
  font-weight: 500;
}

.skill-list li:hover::before {
  transform: scale(1.5);
}

/* Responsive styles */
@media screen and (max-width: 992px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 3.5rem;
  }
  
  .about-skills {
    order: -1;
  }
  
  .skills-container {
    grid-template-columns: repeat(2, 1fr);
  }

  .about-bio h2,
  .about-skills h2 {
    font-size: 1.7rem;
  }
}

@media screen and (max-width: 768px) {
  .about-section {
    padding-top: 80px;
    padding-bottom: 2.5rem;
  }
  
  .skills-container {
    grid-template-columns: 1fr;
    gap: 1.8rem;
  }
  
  .about-bio h2,
  .about-skills h2 {
    font-size: 1.5rem;
  }

  .skill-list {
    grid-template-columns: 1fr;
  }
}
