.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  z-index: 100;
  background-color: var(--background-color);
  box-shadow: var(--shadow);
  transition: var(--transition);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background-color: rgba(248, 250, 252, 0.85); /* Off-white with opacity */
}

.header.scrolled {
  box-shadow: var(--shadow);
  height: 70px;
}

body.dark .header {
  background-color: rgba(30, 27, 75, 0.85); /* Deep indigo with opacity for dark mode */
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 1.5rem;
}

.logo {
  display: flex;
  align-items: center;
}

.logo h1 {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--text-color);
}

.logo h1 span {
  color: var(--primary-color);
}

.nav-buttons {
  display: flex;
  align-items: center;
}

.theme-toggle, .mobile-menu-btn, .language-toggle {
  background: none;
  border: none;
  color: var(--text-color);
  font-size: 1.2rem;
  padding: 0.5rem;
  margin-left: 0.5rem;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: var(--transition);
  width: 40px;
  height: 40px;
}

.language-toggle {
  font-size: 0.9rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  width: auto;
  padding: 0.5rem 0.75rem;
  border-radius: 20px;
  background-color: rgba(79, 70, 229, 0.1); /* Indigo with opacity */
  color: var(--primary-color);
}

.language-toggle svg {
  margin-right: 5px;
  font-size: 1.1rem;
}

.theme-toggle:hover, .mobile-menu-btn:hover {
  background-color: rgba(79, 70, 229, 0.1);
  transform: translateY(-2px);
}

.language-toggle:hover {
  background-color: rgba(79, 70, 229, 0.2);
  transform: translateY(-2px);
}

body.dark .language-toggle {
  background-color: rgba(129, 140, 248, 0.2); /* Light indigo with opacity for dark mode */
  color: var(--accent-color);
}

body.dark .theme-toggle:hover, 
body.dark .mobile-menu-btn:hover,
body.dark .language-toggle:hover {
  background-color: rgba(129, 140, 248, 0.2);
}

.mobile-menu-btn {
  display: none;
}

.nav-menu ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 1.5rem;
}

.nav-menu li {
  position: relative;
}

.nav-menu a {
  color: var(--text-color);
  font-weight: 500;
  font-size: 1rem;
  transition: var(--transition);
  position: relative;
  padding: 0.5rem 0;
  font-family: 'Inter', sans-serif;
  letter-spacing: 0.3px;
}

.nav-menu a:hover {
  color: var(--primary-color);
}

.nav-menu a:hover::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--primary-color);
  transform: scaleX(1);
  transform-origin: bottom left;
  transition: transform 0.3s ease;
}

.nav-menu a::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--primary-color);
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s ease;
}

.nav-menu a.active {
  color: var(--primary-color);
  font-weight: 600;
}

.nav-menu a.active::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--primary-color);
  transform: scaleX(1);
}

/* Tablette */
@media screen and (max-width: 992px) {
  .logo h1 {
    font-size: 1.5rem;
  }
  
  .nav-menu li {
    margin-left: 1.5rem;
  }
}

/* Mobile */
@media screen and (max-width: 768px) {
  .header {
    height: 70px;
  }
  
  .header-container {
    padding: 0 1rem;
  }
  
  .logo h1 {
    font-size: 1.3rem;
  }
  
  .mobile-menu-btn {
    display: flex;
    z-index: 101;
  }
  .nav-menu {
    position: fixed;
    top: 0;
    right: -100%;
    width: 75%;
    max-width: 300px;
    height: 100vh;
    background-color: var(--background-color);
    box-shadow: var(--shadow);
    transition: all 0.3s ease-in-out;
    z-index: 99;
    padding-top: 80px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow-y: auto;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    background-color: rgba(248, 250, 252, 0.95);
  }

  body.dark .nav-menu {
    background-color: rgba(30, 27, 75, 0.95);
  }

  .nav-menu.show {
    right: 0;
  }

  .nav-menu ul {
    flex-direction: column;
    width: 100%;
    padding: 1rem;
  }

  .nav-menu li {
    margin: 0;
    width: 100%;
    text-align: left;
    border-bottom: 1px solid var(--border-color);
  }
  
  .nav-menu li:last-child {
    border-bottom: none;
  }
  
  .nav-menu a {
    display: block;
    padding: 1rem;
    width: 100%;
  }
  
  .nav-menu a.active::after {
    display: none;
  }
    .nav-menu a.active {
    background-color: rgba(79, 70, 229, 0.1); /* Indigo with opacity */
    border-radius: 5px;
  }

  body.dark .nav-menu a.active {
    background-color: rgba(129, 140, 248, 0.2); /* Light indigo with opacity */
  }
  
  body.menu-open {
    overflow: hidden;
  }
  
  /* Overlay when menu is open */
  .menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 98;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
  }
  
  .menu-overlay.show {
    opacity: 1;
    visibility: visible;
  }
  
  .language-toggle {
    font-size: 0.8rem;
    padding: 0.4rem 0.6rem;
  }
}

/* Très petit mobile */
@media screen and (max-width: 360px) {
  .header-container {
    padding: 0 0.75rem;
  }
  
  .logo h1 {
    font-size: 1.1rem;
  }
  
  .theme-toggle, .mobile-menu-btn {
    width: 36px;
    height: 36px;
    font-size: 1rem;
  }
  
  .language-toggle {
    padding: 0.3rem 0.5rem;
    font-size: 0.75rem;
  }
}